.segmentHeader {
  text-align: center;
  margin-bottom: 50px;
  position: relative;
  display: flex;
  justify-content: center;
  margin-bottom: 50px;

  h2 {
    color: #3A8195;
    // font-size: 2.75rem;
    font-size: 3rem;

    transition: opacity 0.3s ease;
    @media screen and (max-width: 1500px) {
      // font-size: 1.5rem;
      font-size: 2.5rem;
    }
    @media screen and (max-width: 757px) {
      font-size: 1.5rem;
    }
  }

  h3 {
    color: #044C85;
    font-size: 5.4375rem;
    @media screen and (max-width: 1500px) {
      font-size: 3rem;
    }
    opacity: 0.04;
    position: absolute;
    // top: 0;
  }

  hr {
    margin: auto;
    margin-top: 10px;
    width: 106px;
  }
}