.digitalMarketing {
  margin-bottom: 100px !important;
  position: relative;
}

.data {
  margin-bottom: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.imgContainer {
  max-width: 417px;
  width: 100%;

  img {
    width: 100%;
    height: auto;
  }
}

.descContainer {
  position: relative;

  .wavImg {
    position: absolute;
    top: -70px;
    width: 100%;
    max-width: 500px;
  }

  h3 {
    color: #374A59;
    position: relative;
    z-index: 1;
    @media screen and (max-width: 575px) {
      margin-top: 20px;
    }
  
    @media screen and (max-width: 1500px) {
      font-size: 1.2rem;
    }
  }

  p {
    position: relative;
    z-index: 1;
    @media screen and (max-width: 1200px) {
      font-size: 0.9rem;
    }
  }
}

.tagline {
  margin: auto;
  margin-top: 50px;
  text-align: center;
  z-index: 1;

  button {
    position: relative;
    z-index: 2;
  }

  button {
    border: none;
    border-radius: 0.5rem;
    span {
      font-weight: bold;
      text-shadow: 0 3px 5px rgba(black, 0.5);
    }
  }
}

.twiggy {
  position: absolute;
  top: 0;
  right: -150px;
  width: 400px;

  transition: opacity 0.3s ease;
  @media screen and (max-width: 1500px) {
    opacity: 0;
  }
}

.octagon1 {
  position: absolute;
  top: 50%;
  left: -100px;
  width: 250px;
  z-index: -1;

  transition: opacity 0.3s ease;
  @media screen and (max-width: 1500px) {
    opacity: 0;
  }
}

.octagon2 {
  position: absolute;
  top: 60%;
  right: -150px;
  width: 400px;
  z-index: -1;

  transition: opacity 0.3s ease;
  @media screen and (max-width: 1500px) {
    opacity: 0;
  }
}
